.button {
    display: inline-block;
    background: #000;
    color: #fff;
    border: none;
    padding: 10px 20px;
    margin: 5px;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    font-size: 15px;
    font-family: inherit;
    text-transform: uppercase;
    transition: -webkit-filter 500ms;
    transition: filter 500ms;
    transition: filter 500ms, -webkit-filter 500ms;
}
